import { FC } from 'react';

import { SocialShareButtonProps } from '../SocialShareButtons.types';

import { getSvg, getLabel } from './SocialShareButton.helpers';
import { StyledButton, StyledIcon } from './SocialShareButton.styles';

const ShareButton: FC<SocialShareButtonProps> = ({
  type,
  url,
  withThemeColors,
  className,
}) => {
  const label = getLabel(type);
  const svg = getSvg(type);

  return (
    <StyledButton
      href={url}
      target="_blank"
      rel="noreferrer"
      aria-label={label}
      $type={withThemeColors ? undefined : type}
      className={className}
    >
      <StyledIcon svg={svg} size="large" />
    </StyledButton>
  );
};

export default ShareButton;
