import { SocialMedia } from '../SocialShareButtons.types';
import svg from '../util/svg';

export const getSvg = (type: SocialMedia) => {
  if (!svg[type]) {
    throw new Error(`No icon for ${type}`);
  }

  return svg[type];
};

export const getLabel = (type: SocialMedia) => {
  switch (type) {
    case SocialMedia.EMAIL:
      return 'Share via email';
    default:
      return `Share on ${type[0].toUpperCase() + type.slice(1)}`;
  }
};
