import { FC } from 'react';

import SocialShareButton from './SocialShareButton';
import { Wrapper } from './SocialShareButtons.styles';
import {
  SocialShareButtonsProps,
  SocialShareButtonProps,
} from './SocialShareButtons.types';

const SocialShareButtons: FC<SocialShareButtonsProps> = ({
  buttons,
  withThemeColors = false,
  mobileOnlyButtons = [],
}) => {
  if (!Array.isArray(buttons) || !buttons?.length) {
    return null;
  }

  return (
    <Wrapper>
      {buttons.map((button: SocialShareButtonProps, i: number) => (
        <SocialShareButton
          key={i}
          type={button.type}
          url={button.url}
          withThemeColors={withThemeColors}
          {...(mobileOnlyButtons.includes(button.type) && {
            className: 'mobile-only',
          })}
        />
      ))}
    </Wrapper>
  );
};

export default SocialShareButtons;
